.homepage-child {
  position: absolute;
  height: 73.05%;
  width: 80.39%;
  top: 13.48%;
  right: 9.83%;
  bottom: 13.48%;
  left: 9.77%;
  border-radius: var(--br-22xl);
  background-color: var(--color-gray-300);
  border: 7px solid var(--color-white);
  box-sizing: border-box;
}
.portfolio-is-currently {
  position: absolute;
  top: calc(50% - 238px);
  left: calc(50% - 338px);
}
.httpslottiefilescomanimat-icon {
  position: absolute;
  top: calc(50% - 165px);
  left: calc(50% - 250px);
  width: 500px;
  height: 500px;
  object-fit: cover;
}
.homepage {
  position: relative;
  background: linear-gradient(118.82deg, #dbb09e, #ffc3d2);
  width: 100%;
  height: 1024px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-41xl);
  color: var(--color-gray-200);
  font-family: var(--font-lilita-one);
}
